div.hero {
    background: $gradient-1;
    position: relative;
    &::before {
      content: "";
      //background-image: url(/images/build/hero-bg.jpg);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      position: absolute;
      z-index: 1;
      right: 0;
      top: 0;
      height: 100%;
      width: 100%;
    }
    .hero-intro-text-container {
      position: relative;
      z-index: 10;
      padding: 169px 0 147px 0;
      @include mobile {
        padding: 50px 0;
      }
    }
    .hero-intro-text {
      position: relative;
      z-index: 2;
      text-align: center;
      .title {
        margin-bottom: 1rem;
      }
      .top-lead {
        color: $tertiary-200;
        font-size: 20px;
        margin: 0;
      }
      .lead {
        margin-bottom: 1rem;
        font-weight: 600;
      }
      h1.poetsenone {
        margin-bottom: 1.3rem;
      }
    }
    .hero-graphic-left {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      z-index: 3;

      @media(max-width: 1600px){
        display: none;
      }
    }
    .hero-graphic-right {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;

      @media(max-width: 1600px){
        display: none;
      }
    }
    .hero-cards {
      position: relative;
      z-index: 3;
    }
  }