header {
    .top-nav {
      padding: 0.75rem 1.5rem;
      background-color: $primary-200;
      font-size: 16px;
    }
    .main-nav {
      background-color: $primary;
      padding: 0.75rem 1.5rem;
      @include touch {
        padding: 0;
      }
      .navbar-container {
        @include touch {
          padding: 0.75rem 0.75rem 0 0.75rem;
        }
        .navbar {
          .navbar-brand {
            .navbar-burger{
              height: 2rem !important;
              width: 2rem !important;
            }
            @include touch {
              padding: .7rem 1.5rem;
            }
            align-items: center;
            .navbar-burger {
              margin-left: 0;
            }
          }
          .navbar-menu {
            @include touch {
              background: $gradient-3;
              padding: 0;
            }
            .customer-support-mobile {
              background-color: $primary-200;
              padding: 1.3rem 1.5rem;
  
              a {
                font-size: 16px;
              }
            }
            .navbar-end {
              justify-content: space-evenly !important;
              margin-left: unset !important;
              align-items: center !important;
              width: 100%;
              @include touch {
                background: $gradient-3;
                padding: 1.5rem;
              }
              li {
                list-style: none;
                @include touch {
                  margin-bottom: 30px;
  
                  &:last-child {
                    margin: 0;
                  }
                }
                a {
                  font-size: 24px;
                  @include until-widescreen {
                    font-size: 20px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }