/***** VARIABLES *****/
//FONTS
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;500;600;700;800&family=Open+Sans:wght@800&display=swap");
@font-face {
  font-family: PoetsenOneFont;
  src: url("font/PoetsenOne-Regular.ttf");
}

/***** BRANDS *****/
$gradient-1: linear-gradient(180deg, #013879 0%, #1486c3 100%);
$gradient-2: linear-gradient(180deg, #1486c3 0%, #013879 100%);
$gradient-3: linear-gradient(180deg, #034384 0%, #1385c2 100%);
$primary: #013879;
$primary-200: #062f61;
$secondary: #1486c3;
$tertiary: #f8cf03;
$tertiary-200: #ffb800;
$light-green: #c9db00;
$white: #fff;
$black: #000;

/**** SETTING UP NEW VARIBLES *****/
$family-primary: "Noto Sans", sans-serif;
$family-secondary: "Open Sans", sans-serif;
$family-tertiary: PoetsenOneFont;
$icon-text-spacing: 0.8em;
$body-color: $white;
$body-family: $family-primary;
$body-weight: 400;
$body-size: 24px;
$link: $white;
$link-hover: $tertiary;
$dropdown-item-color: $primary-200;
$dropdown-content-z: 50;
$tag-radius: 40px;

/***** BULMA FRAMEWORK *****/
@import "../../node_modules/bulma/sass/utilities/_all.sass";
@import "../../node_modules/bulma/sass/base/_all.sass";
@import "../../node_modules/bulma/sass/grid/columns.sass";
@import "../../node_modules/bulma/sass/utilities/mixins.sass";
@import "../../node_modules/bulma/sass/elements/icon.sass";
@import "../../node_modules/bulma/sass/components/dropdown.sass";
@import "../../node_modules/bulma/sass/helpers/spacing.sass";
@import "../../node_modules/bulma/sass/helpers/visibility.sass";
@import "../../node_modules/bulma/sass/helpers/flexbox.sass";
@import "../../node_modules/bulma/sass/components/navbar.sass";
@import "../../node_modules/bulma/sass/helpers/typography.sass";
@import "../../node_modules/bulma/sass/components/level.sass";
@import "../../node_modules/bulma/sass/layout/section.sass";
@import "../../node_modules/bulma/sass/elements/tag.sass";

/***** BODY *****/
body{
  overflow: hidden;
}

/***** CONTAINERS *****/
.container {
  margin: 0 auto;
  max-width: 1525px;
  &.is-widescreen {
    max-width: 1173px;
  }
}

/***** BG SECTIONS *****/
section.bg-gradient1{
  background: $gradient-1;
}
section.bg-gradient2{
  background: $gradient-2;
}

/***** TYPOGRAPHY *****/
h1 {
  @include widescreen {
    font-size: 60px;
  }
  font-weight: 800;
  line-height: 1;
  font-family: $family-secondary;
  font-size: calc(1.375rem + 1.5vw);
}
h1.poetsenone {
  @include widescreen {
    font-size: 60px;
  }
  font-family: $family-secondary;
  font-weight: 400;
  background: linear-gradient(180deg, #faea99 0%, #f8cf03 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: calc(1.375rem + 1.5vw);
}
h2 {
  @include widescreen {
    font-size: 50px;
  }
  font-weight: 800;
  font-family: $family-secondary;
  font-size: calc(1.325rem + .9vw);
}
h3 {
  @include widescreen{
    font-size: 35px;
  }
  font-size: calc(1.3rem + .6vw);
  font-weight: 700;
}
h4 {
  font-size: 30px;
  font-weight: 800;
}
p {
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 0.5rem;
}
a {
  text-decoration: none;
}
li{
  list-style: none;
}

/***** CUSTOM BULMA COMPONENTS *****/
@import './abstract/custom-bulma-components';

/*****CUSTOM COMPONENTS*****/
@import './abstract/custom-components';

/***** BLOCKS *****/
@import './layout/header';
@import './layout/footer';
