a {
    .icon-text {
      align-items: center;
      .nav-icon {
        margin-right: 0.4em;
      }
    }
    &:hover {
      .icon-text {
        .icon {
          svg {
            path {
              fill: $tertiary;
            }
          }
        }
        .nav-icon {
          svg {
            path {
              fill: $tertiary;
            }
          }
        }
      }
    }
  }
  .dropdown {
    .dropdown-trigger {
      .button.is-primary {
        background-color: transparent;
        color: $white;
      }
    }
  }
  .dropdown {
    .dropdown-item {
      color: $primary-200;
      font-size: 16px !important;
      a {
        color: $primary-200;
        font-size: 16px !important;
      }
    }
  }
  .dropdown {
    .profile {
      width: 2rem;
    }
  }
  .dropdown.language {
    .dropdown-menu {
      @include touch {
        left: unset;
        right: 0;
      }
    }
  }

  .tag.is-blue{
   background-color: $secondary;
   color: $light-green;
  }
  .tag.is-yellow{
    background-color: $tertiary;
    color: $white;
  }