//CARDS
.card-1 {
    flex: 1;
    border-radius: 10px;
    text-align: center;
    background: $gradient-1;
    border-bottom: 7px solid $tertiary;
    padding: 30px;
    h4 {
      color: $tertiary;
      margin-bottom: 8px;
    }
    p {
      font-size: 20px;
      max-width: 80%;
      margin: 0 auto;
    }
    .icon-text {
      justify-content: center;
      .icon {
        margin-right: 0.3rem;
      }
    }
  }
  .card-2 {
    flex: 1;
    border-radius: 10px;
    background-color: $primary;
    padding: 30px;
    .content {
      border-radius: 10px;
      min-height: 172px;
      &.bg{
        background-color: rgba(255, 255, 255, 0.1);
      }
    }
    .level{
      .level-right{
        h3{
          display: inline-block;
          position: relative;
         
          @include mobile {
            font-size: 24px !important;
          }
          &::before{
            content: url('https://api.iconify.design/bxs/down-arrow.svg?color=%23c9db00');
            position: absolute;
            top: -1.2rem;
            left: 50%;
            transform: translateX(-50%);
          }
        }
      }
    }
  }
  .card-3 {
    flex: 1;
    background-color: $secondary;
    border-radius: 10px;
    padding: 30px;
  } 


//BUTTONS
  .button {
    border-radius: 8px;
    font-weight: 500;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    cursor: pointer;
    padding: 7px 23px;
    min-width: 122px;
    text-align: center;
  }
  
  .button.is-primary {
    background-color: $tertiary;
    color: $black;
    &:hover {
      background-color: #ebc403;
      color: $black;
    }
  }
  .button.is-medium {
    font-size: 18px;
  }
  .button.is-large {
    font-size: 22px;
  }
  .button.is-small {
    font-size: 16px;
  }
